import React from 'react';
import { Breakpoint } from 'react-socks';
import Logo from '../logo/logo.component';
import MobileNav from '../mobileNav/mobileNav.component';
import DesktopNav from '../desktopNav/desktopNav.component';

const MobileHeader = () => {
  const [enableBkg, setEnableBkg] = React.useState(false);
  React.useEffect(() => {
    window.addEventListener('scroll', function () {
      if (window.scrollY !== 0) {
        setEnableBkg(true);
      } else {
        setEnableBkg(false);
      }
    });
    return () => {
      window.removeEventListener('scroll', null);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  const [toggleNav, setToggleNav] = React.useState(false);
  return (
    <header className="mobile-header-container">
      <div
        className={`mobile-header-top-bar ${
          enableBkg || toggleNav ? 'nav-scroll-bkg' : ''
        }`}
      >
        <Logo />
        <Breakpoint medium down>
          <div
            onClick={() => setToggleNav(!toggleNav)}
            className={`hamburger ${toggleNav ? 'is-active' : ''}`}
          >
            <span className="line"></span>
            <span className="line"></span>
            <span className="line"></span>
          </div>
        </Breakpoint>
        <Breakpoint large up>
          <DesktopNav />
        </Breakpoint>
      </div>
      <Breakpoint medium down>
        {toggleNav ? <MobileNav setToggleNav={setToggleNav} /> : null}
      </Breakpoint>
    </header>
  );
};

export default MobileHeader;
