import React from 'react';
import { Breakpoint } from 'react-socks';
import Fade from 'react-reveal/Fade';
import downArrow from '../assets/down-arrow.svg';
import outsourcing from '../assets/outsourcing-icon.svg';
import productSourcing from '../assets/product-sourcing-icon.svg';
import importExport from '../assets/import-export-icon.svg';
import ServicesCardSmall from '../components/servicesCardSmall/servicesCardSmall.component';
const Home = () => {
  const [nzToVnNum, setNzToVnNum] = React.useState(0);
  const icons = [importExport, outsourcing, productSourcing];

  const countUp = (num) => {
    for (let i = 0; i <= num; i++) {
      setTimeout(() => {
        setNzToVnNum(i);
      }, 100);
    }
  };

  const MobileText = () => {
    return (
      <h2>
        Dedicated to Increasing Trade Between <br></br> New Zealand
        <br></br> and Vietnam
      </h2>
    );
  };

  const DesktopText = () => {
    return (
      <h2>
        Dedicated to Increasing Trade Between <br></br> New Zealand and Vietnam
      </h2>
    );
  };

  return (
    <div className="homePage-container">
      {/* ------------ Intro Start ------------ */}{' '}
      <Fade>
        <section className="homePage-intro">
          <Fade delay={1000}>
            <div className="homePage-intro-overlay">
              <Breakpoint medium down>
                <MobileText />
              </Breakpoint>
              <Breakpoint large up>
                <DesktopText />
              </Breakpoint>
              <img
                src={downArrow}
                alt="down arrow"
                className="homePage-intro-down-arrow"
              />
            </div>
          </Fade>
        </section>{' '}
      </Fade>
      {/* ------------ Intro End ------------ */}
      {/* ------------ About Us Blurb Start ------------ */}
      <section className="homePage-about-us">
        <Fade>
          <h3>About Us</h3>
          <p>
            Lorem ipsum dolor sit amet consectetur, adipisicing elit. Itaque
            officia asperiores natus ipsum, quaerat nobis quis consequuntur
            sapiente autem sed voluptas illo voluptates, aut harum. <br></br>
            <br></br>
            Eos cumque quos voluptate temporibus.Consequatur voluptas ducimus id
            ea eius totam omnis dolores corrupti, dolorum accusamus sint ex
            voluptatem hic quam necessitatibus minima odio est consectetur!
            Molestias dolores provident quaerat quia placeat, in quae.
          </p>
        </Fade>
      </section>
      {/* ------------ About Us Blurb End ------------ */}
      {/* ------------ Services Start ------------ */}
      <section className="homePage-services">
        <Fade>
          <h3>Services</h3>
          <div className="homePage-services-cards-container">
            {icons.map((icon, index) => (
              <ServicesCardSmall key={index + 1} icon={icon} />
            ))}
          </div>
        </Fade>
      </section>
      {/* ------------ Services End ------------ */}
      {/* ------------ Current Trade Start ------------ */}
      <section className="homePage-current-trade">
        <Fade>
          <h3>Current Trade</h3>
          <div className="current-trade-total-cards-container">
            <div className="current-trade-total-card">
              <span>${nzToVnNum}m</span>
              <p onClick={() => countUp(745)}>NZ Exports to Vietnam</p>
            </div>
            <div className="current-trade-total-card">
              <span>$986.5m</span>
              <p>Vietnam Exports to NZ</p>
            </div>
          </div>
        </Fade>
      </section>
      {/* ------------ Current Trade End ------------ */}
    </div>
  );
};

export default Home;
